export interface IService {
	_id?: string;
	title: string;
	description: string;
	recommendations: string;
	priceVariant: {
		default: number;
		top: number;
		kmn: number;
	};
	price?: number;
	time: number;
	tags: string[];
	score: number;
	active: boolean;
}

export class Service implements IService {
    _id?: string;
	public title: string;
	public description: string;
	public recommendations: string;
	public priceVariant: {
		default: number;
		top: number;
		kmn: number;
	};
	price?: number;
	public time: number;
	public tags: string[];
	public score: number;
	public active: boolean = true;

	constructor(object?: IService){
		if(object) {
			this.setParams(object);
		}
	}

	setParams(options: {
		_id?: string;
		title?: string;
		description?: string;
		recommendations?: string;
		priceVariant?: {
			default: number;
			top: number;
			kmn: number;
		};
		price?: number;
		time?: number;
		tags?: string[];
		score?: number;
		active?: boolean;
	}) {
		this._id = options._id || null;
		this.title = options.title || null;
		this.description = options.description || null;
		this.recommendations = options.recommendations || null;
		this.priceVariant = options.priceVariant || {
			default: 0,
			top: 0,
			kmn: 0
		};
		this.price = options.price || null;
		this.time = options.time || 0;
		this.tags = options.tags || [];
		this.score = options.score || 0;
		this.active = options.active || false;
	}

	// get price() {
	// 	return this.priceVariant['default'];
	// }

	finishPrice(name?: string) {
		
	}

	clear() : void {
		this._id = null;
		this.title = null;
		this.description = null;
		this.recommendations = null;
		this.priceVariant = {default: null, top: null, kmn: null};
		this.price = null;
		this.time = null;
		this.tags = [];
		this.score = 0;
		this.active = false;
	}
}



