import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { OrderBy } from '../../../shared/pipes/orderby.pipe';

import { Service } from "../../service";
import { IServicesListOptions } from '../servicesListOptions';
import { ToolsService } from '../../../shared/tools.service';

@Component({
  selector: 'app-services-list',
  templateUrl: './services-list.component.html',
  styleUrls: ['./services-list.component.css']
})
export class ServicesListComponent implements OnInit {
  @Input('services') services: Service[] = [];
  @Input('options') set options(options: IServicesListOptions) {
    this._options = {
      tools: {
        edit: !!options.tools.edit,
        delete: !!options.tools.delete,
        check: !!options.tools.check,
        add: !!options.tools.add
      },
      price: !!options.price,
      priceVariant: !!options.priceVariant,
      tags: !!options.tags
    }
  };

  @Output('check') eCheck: EventEmitter<Service> = new EventEmitter<Service>();
  @Output('delete') eDelete: EventEmitter<Service> = new EventEmitter<Service>();
  @Output('edit') eEdit: EventEmitter<Service> = new EventEmitter<Service>();
  @Output('add') eAdd: EventEmitter<Service> = new EventEmitter<Service>();

  currentItem: Service;
  selectedItems: Service[] = [];
  _options: IServicesListOptions = {
    tools: {
      edit: true,
      delete: true,
      add: false,
      check: false
    },
    price: true,
    priceVariant: true,
    tags: true
  };
  get options() { return this._options; }

  constructor() { }

  ngOnInit() {
   
  }

  addItem(item) {
    this.selectItem(item);
  }

  deleteItem(item) {
    this.currentItem = item;
    this.eDelete.emit(item);
  }

  editItem(item) {
    this.currentItem = item;
    this.eEdit.emit(item);
  }

  clickItem(item) {
    if(this.isSelect(item)) this.unSelectItem(item);
    else this.selectItem(item);
    
    this.eCheck.emit(item);
  }

  selectItem(item) {
    this.selectedItems.push(item);
  }

  unSelectItem(item) {
    this.selectedItems.find((found, index) => {
      if(found._id == item._id) {
        this.selectedItems.splice(index, 1);
        return true;
      }
    });
  }

  isSelect(item: Service) {
    return !!this.selectedItems.find((found) => {
      if(found._id === item._id) return true;
    });
  }

  getSelectedItems() : Service[] {
    return this.selectedItems;
  }

  clearSelected() {
    this.selectedItems = []; 
  }

  onAdd(item: Service) {
    this.addItem(item);
    this.eAdd.emit(item);
  }

}



