import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../../shared/shared.module';

import { ServicesListComponent } from './services-list/services-list.component';

import { OrderBy } from '../../shared/pipes/orderby.pipe';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    SharedModule
  ],
  declarations: [ServicesListComponent, OrderBy],
  exports: [ServicesListComponent]
})
export class ServicesWidgetModule { }


